body {
  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.wraper {
  background: linear-gradient(#7b7b7b, #5ccab5);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
}
.form-container {
  background-color: #2c5046;
  height: 70vh;
  padding: 100px 50px 0 50px;
  margin: auto;
}
input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid #ffffff;
  width: 100%;
  display: block;
  padding-bottom: 4px;
  color: #ffffff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  font-size: 20px;
}
input[type="text"] {
  outline: none;
}
button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 4px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #000;
  color: #ffffff;
  font-weight: 100;
}
button:active {
  outline: none;
}
.info {
  width: 100%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}
.info-key {
  color: #ffffff;
  font-weight: 400;
  border-bottom: 2px solid rgba(255, 255, 255, 0.6);
}
.info-key:last-child {
  border: 0;
}
.info-value {
  color: #ffffff;
  font-weight: 200;
}
